
#map{
	width: 100%;
	height: 35vh;
	box-sizing: border-box;
	margin-top: 64px;
}
#detail-info{
	background-image: url('../assets/preview.jpg');
	width: 100%;
	padding: 10px;
	box-sizing: border-box;
	background-size: cover;
	background-position: center center;
}
.detail-info-container{
	width: 100%;
	height: 100%;
	background-color: white;
	overflow-y: scroll;
	box-sizing: border-box;
	padding: 20px;
	box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}
.detail-info-container h1 { 
	font-size: 17px;
	font-weight: bold;
	display: inline-block;
	margin: 0px;
}
.detail-info-container p {
	font-weight: lighter;
	display: inline;
	font-size: 15px;
	line-height: 1.35;
	padding-left: 5px;
}
.detail-apply {
	text-align: center;
	margin-bottom: 30px;
}
.detail-apply button{
	padding: 15px 25px;
	font-size: 15px;
	background-color: #1976D2;
	color: white;
	border-radius: 100px;
	font-weight: bold;
	box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
	border : none;
}
.left-border{
	padding-left: 5px;
	border-left: 4px solid #1976D2;
}
.detail-div{
	margin-bottom: 30px;
	word-wrap: break-word;
	word-break: break-all;
}
.geo-link{
	position: absolute;
    top: 120px;
    right: 0px;
    padding: 10px;
    font-size: 12px;
    color: white;
    background: rgba(25,118,210,0.7);
    z-index: 2;
    font-weight: bold;
    text-decoration: none;
    display: block;
}
.detail-back-button{
	position: absolute;
	top: 60px;
	left: 0px;
	font-weight: bold;
	background: rgba(25,118,210,0.8);
	color : white;
	font-size: 40px;
	padding: 25px 35px;
}
.detail-bar{
	background : #1976D2;
	border : none;
	display: inline-block;
	width: 100%;
	text-align: center;
	padding: 20px;
	box-sizing: border-box;
	box-shadow: 0 5px 5px 1px rgba(0,0,0,0.5);
	margin-bottom: 10px;
	position: fixed;
	top: 0;
	z-index: 1000;
}
.detail-bar img{
	display: inline-block;
	height: 30px;
	width: 30px;
	/*padding: 36.5px;*/
}
.detail-bar p{
	color: white;
	font-size: 15px;
	text-align: center;
	padding: 0px 40px;
	width: 100%;
	margin: 0px;
	box-sizing: border-box;
	font-weight: bold;
}
.detail-bar button {
	position: absolute;
	left: 10px;
	top: 14px;
	background : none;
	border : none;
	float: left;
}




